@import url(https://fonts.googleapis.com/css?family=Roboto:400,500&display=swap&subset=cyrillic);
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  text-decoration: none;
}
strong {
  font-weight: 500;
}

body {
  font-family: "Roboto", sans-serif;
  color: #222;
  line-height: 1.35;
}

a,
a:active,
a:visited {
  color: #222;
}

.page__header {
  background: #f3f3f3;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.nav__menu {
  display: flex;
  align-items: center;
}

.nav__item {
  display: block;
}

.nav__link {
  color: #222;
  text-decoration: none;
}

.nav__link:hover {
  text-decoration: underline;
}

.nav__link.active {
  text-decoration: underline;
}

.nav__item + .nav__item {
  margin-left: 15px;
}

.page__main {
  padding: 20px;
}

/* list-info */
.list-info {
  margin-bottom: 15px;
}
/* end of list-info */

/* film-page */
.film-page__wrapper {
  display: flex;
}

.film-page__image {
  max-width: 300px;
  max-height: 300px;
}

.film-page__info {
  margin-left: 30px;
}

.film-page__gallery {
  margin-top: 30px;
}
/* end of film-page */

.films-listing {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.films-listing__title {
  font-weight: 500;
  margin-bottom: 10px;
}

.films-listing__item {
  display: flex;
  align-items: flex-start;
  min-height: 160px;
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px solid #ccc;
  box-sizing: border-box;
}

.films-listing__item:last-child {
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #ccc;
}

.films-listing__image {
  max-width: 100px;
  max-width: 100px;
}

.films-listing__info {
  margin-left: 20px;
  flex-grow: 1;
}

.films-listing__link {
  display: inline-block;
  margin-top: 10px;
  background: #eee;
  border: 1px solid #aaa;
  border-radius: 2px;
  padding: 0 10px;
  -webkit-transition: all 225ms ease-in-out;
  transition: all 225ms ease-in-out;
}

.films-listing__link:hover {
  background: #fff;
}

.pagination {
  display: flex;
  flex-wrap: wrap;
  margin: 10px -3px -3px -3px;
}

.pagination__item {
  padding: 2px 6px;
  background: #eee;
  border: 1px solid #aaa;
  -webkit-transition: all 225ms ease-in-out;
  transition: all 225ms ease-in-out;
  cursor: pointer;
  margin: 0 3px 3px 3px;
}

.pagination__item:hover {
  background: #fff;
}

.pagination__item__active {
  background: #fff;
  border: 1px solid #355;
}

.pagination__item__hiden {
  background: #000;
}

